import * as React from "react";
import Link from "../Link";
import "./styles.scss";

const Paginator = (props) => {
    const { curPageNum, numOfPages } = props;
    if (numOfPages <= 1) {
        return null;
    }

    console.log({
        curPageNum,
        numOfPages,
    })
    const prevUrl = curPageNum === 2 ? '/' : `/blogs/archive/${curPageNum - 2}`;
    const nextUrl = curPageNum === numOfPages ? '/' : `/blogs/archive/${curPageNum}`;
    return (
        <div className="PaginatorContainer">
            {curPageNum !== 1 &&
                <Link className="PaginatorPrev" href={prevUrl} disabled={curPageNum === 1}>Prev</Link>
            }
            {curPageNum !== numOfPages &&
                <Link className="PaginatorNext" href={nextUrl} disabled={curPageNum === numOfPages}>Next</Link>
            }
        </div>
    );
}

export default Paginator;