import { graphql } from "gatsby"
import React from "react";
import {MainSectionWrapper} from "../layout";
import CommonHead from "../layout/components/Head";
import HomeCardLink from "../layout/components/HomeCardLink";
import Paginator from "../layout/components/Paginator";

import {GatsbyImage, getImage} from "gatsby-plugin-image"

const PageTemplate = (props) => {
  if (!props.pageContext || !props.data) {
    return null;
  }

  const {pageContext, data} = props;
  const {curPageNum, numOfPages} = pageContext;

  return (
    <MainSectionWrapper>
      {data.allGoogleDocs.nodes.map(node => {
        return (
          <HomeCardLink key={node.id} {...node} />
        );
      })}

      <Paginator curPageNum={curPageNum} numOfPages={numOfPages} />
    </MainSectionWrapper>
  )
};

export function Head(props) {
  const data = {
    page: {
      name: "Home",
    }
  }
  return <CommonHead {...props} data={data} />;
}

export const query = graphql`
  query Blog($limit: Int, $skip: Int, $directory: String) {
    allGoogleDocs(
      filter: {directory: {eq: $directory}, page: {eq: true}, index: {eq: false}}
      sort: {createdTime: DESC}
      limit: $limit
      skip: $skip
    ) {
      nodes {
        id
        name
        path
        createdTime
        cover {
          image {
            childImageSharp {
              fluid(maxWidth: 200, quality: 85) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
  }
`;

export default PageTemplate;