import * as React from "react";
import { H2, H3 } from "../Header";
import Link from "../Link";
import Separator from "../Separator";
import moment from "moment";

import Img from "gatsby-image";

import "./styles.scss";

const HomeCardLink = (props) => {
    const { cover, createdTime, name, path } = props;

    const creation = moment(createdTime).format('MM/DD/YYYY');
    return (
        <>
            <Link href={path} useTheming={false}>
                <div className="HomeCardLink">
                    {cover?.image &&
                        <div className="HomeCardCoverImage">
                            <Img
                                fluid={cover.image.childImageSharp.fluid}
                                title={cover.title}
                                alt={cover.alt}
                            />
                        </div>
                    }
                    <div className="HomeCardTitleAndDesc">
                        <H2>{name}</H2>
                        <H3>{creation}</H3>
                    </div>
                </div>
            </Link>
            <Separator />
        </>
    );
};

export default HomeCardLink;